.jumbotron {
  display: flex;
}

.jumbotron > div {
  display: flex;
  flex-direction: column;
  text-align: left;
  position: relative;
  align-self: flex-end;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 1);
  color: #fff;
  margin: 0 0 -25px 10px;
}

.jumbotron > .play {
  z-index: 10;
  position: absolute;
  align-self: center;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  border-radius: 50%;
  width: fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
}
.jumbotron > .play > svg {
  width: clamp(2rem, 5vw, 5rem);
  height: clamp(2rem, 5vw, 5rem);
}

h3 {
  font-size: clamp(1rem, 2.5vw, 1.5rem);
}

.jumbotron > div > h3 {
  font-size: clamp(1.5rem, 2.5vw, 3rem);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 20ch;
}

.jumbotron > div > p,
.jumbotron > div > b {
  font-size: clamp(1rem, 2.5vw, 1.2rem);
}

.jumbotron > div > div > button {
  width: fit-content;
}
.modal-content .close {
  color: #fff !important;
}
.play__2 {
  display: none !important;
  margin-right: 0.5rem;
}

.hideStart {
  position: fixed;
  bottom: 5%;
  left: 90%;
  visibility: hidden;
  opacity: 0;
  border-radius: 50%;
  font-size: 2rem;
  transform: translateY(10px);
  transition: all 300ms ease-out;
}

.showStart {
  visibility: visible;
  opacity: 1;
  transition: all 300ms ease-out;
  transform: translateY(0);
}

.genre .btn {
  background-color: transparent !important;
  border-radius: 40px;
  padding: 2px 10px;
  font-weight: normal !important;
  color: ghostwhite;
}
@media only screen and (max-width: 992px) {
  .jumbotron > .play {
    left: 60%;
  }
}

@media only screen and (max-width: 600px) {
  .jumbotron > .play {
    display: none;
  }
  .play__2 {
    display: inline-block !important;
  }
}
